@import "helpers/all";
//
// @import "generated/sprite-svg";
// @import "generated/sprite-png";
@import "common";
@import "layouts";
@import "main";
@import "variables";


.archive {

    margin-top: 40px;



	&__header {



    }



    &__sub-header {

        padding: 40px 0px 10px;

    }

}




.donation {

    color: $black;

    text-decoration: none;

    background-color: $white;

    padding: 5px 10px;

    border-radius: 5px;

    transition: 0.5s;

    box-shadow: inset 0px -3px 0 0 #0e0e0e;

}



.donation:hover {

    background: linear-gradient(#f5ae00, #f59500) #f5ae00;

}




.main {

    display: flex;

    width: 60%;

    min-height: 100%;

    justify-content: center;

    align-items: center;

    text-align: center;

    color: $white;

    flex-direction: column;

    margin-top: 80px;

    margin-bottom: 80px;



    @include r(1023) {

        width: 100%;

    }



    &__header {

        display: block;



        @include r(1023) {

            font-size: 1.6em;

        }

    }



    &__donation {

        display: block;

        margin: 20px 0px;

        padding-bottom: 7px;

        border-radius: 6px;

    }

}






.social-buttons {

    display: flex;



    &__item {

        margin: 20px;

        color: $white;



        @include r(1023) {

            margin: 10px;

        }

    }



    &__img {

        width: 50px;

        margin: auto;



        @include r(1023) {

            width: 30px;

        }

    }



    &__link {

        display: flex;

        text-decoration: none;

        color: $white;

        font-family: 'Bad Script', cursive;

        flex-direction: column;

        text-align: center;

        text-transform: uppercase;

    }



    &__text {

        padding-top: 10px;

        font-size: 0.8em;



         @include r(1023) {

            font-size: 0.6em;

        }

    }

}




.t-shirt {

    margin-top: 40px;



    &__link {

        display: block;

        color: $white;

        text-decoration: none;

        font-family: 'Bad Script', cursive;

        padding: 10px;

        outline: none;

        border-radius: 3px / 100%;

        background-image:

        linear-gradient(45deg, rgba(255,255,255,.0) 30%, rgba(255,255,255,.8), rgba(255,255,255,.0) 70%),

        linear-gradient(to right, rgba(255,255,255,1), rgba(255,255,255,0) 20%, rgba(255,255,255,0) 90%, rgba(255,255,255,.3)),

        linear-gradient(to right, rgba(125,125,125,1), rgba(255,255,255,.9) 45%, rgba(125,125,125,.5)),

        linear-gradient(to right, rgba(125,125,125,1), rgba(255,255,255,.9) 45%, rgba(125,125,125,.5)),

        linear-gradient(to right, rgba(223,190,170,1), rgba(255,255,255,.9) 45%, rgba(223,190,170,.5)),

        linear-gradient(to right, rgba(223,190,170,1), rgba(255,255,255,.9) 45%, rgba(223,190,170,.5));

        background-repeat: no-repeat;

        background-size: 200% 100%, auto, 100% 2px, 100% 2px, 100% 1px, 100% 1px;

        background-position: 200% 0, 0 0, 0 0, 0 100%, 0 4px, 0 calc(100% - 4px);

        box-shadow: rgba(0,0,0,.5) 3px 10px 10px -10px;

    }

}



.t-shirt__link:hover {

    transition: .5s linear;

    background-position: -200% 0, 0 0, 0 0, 0 100%, 0 4px, 0 calc(100% - 4px);

}




.youtube {

	position:relative;

	margin: auto;

	overflow:hidden;

}



html {
    position: relative;
    font-size: 100%;
    width: 100vw;
    height: 100%;
    overflow-x: hidden;

    @include r(1440) {
        font-size: 100%;
    }

    @include r(1200) {
        font-size: 90%;
    }

    @include r(1024) {
        font-size: 80%;
    }

    @include r(640) {
        font-size: 60%;
    }

}

body {
    font-family: $font;
    width: 100vw;
    height: 100%;
    position: relative;
    background-image: url('../img/background.jpg');
    background-repeat: none;
    background-position: center;
    background-size: cover;
     background-attachment: fixed;
}
